<template>
  <div>
  <v-layout row justify-center>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Open Form</v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="headline">Custom Events</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm6>
                <v-select
                  :items="size"
                  v-model="eventSize"
                  label="Event Size"
                  single
                  required
                ></v-select>
              </v-flex>
              <v-flex xs12 sm6>
                <v-select
                  :items="intensity"
                  v-model="energy"
                  label="Energy Intensity"
                  single
                  required
                ></v-select>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
          <v-btn color="blue darken-1" flat @click="() => updateData()">Add</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
    <v-data-table
      :headers="headers"
      :items="events"
      :expand="expand"
      item-key="name"
    >
      <template slot="items" slot-scope="props">
        <tr>
          <td>{{ props.item.name }}</td>
          <td class="text-xs-right">{{ props.item.demand }}</td>
          <td class="text-xs-right">{{ props.item.lighting }}</td>
          <td class="text-xs-right">{{ props.item.plug }}</td>
          <td class="text-xs-right">{{ props.item.vertical }}</td>
          <td class="text-xs-right">{{ props.item.mechanical }}</td>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        dialog: false,
        size: ["Small","Medium","Large"],
        intensity: ['Low','Moderate','High'],
        expand: false,
        headers: [
          {
            text: 'Floor',
            align: 'left',
            sortable: false,
            value: 'name'
          },
          { text: 'Total Demand (kwh)', value: 'demand' },
          { text: 'Lighting (kwh)', value: 'lighting' },
          { text: 'Plug Loads (kwh)', value: 'plug' },
          { text: 'Vertical Transportation (kwh)', value: 'vertical' },
          { text: 'Mechanical (kwh)', value: 'mechanical' }
        ],
        events: [
          {
            name: 'Basement',
            demand: 50,
            lighting: 10,
            plug: 20,
            vertical: 10,
            mechanical: 10
          },
          {
            name: '1st Floor',
            demand: 80,
            lighting: 20,
            plug: 10,
            vertical: 30,
            mechanical: 20
          },
          {
            name: '2nd Floor',
            demand: 100,
            lighting: 20,
            plug: 30,
            vertical: 30,
            mechanical: 20
          }
        ]
        }
      },
    methods:{
      updateData(){
        var setting1=this.eventSize
        var setting2=this.energy
        var addition
        var factor
        if (setting1 == 'Small'){
            addition = 10
        }
        if (setting1 == 'Medium'){
            addition = 50
        }
        if (setting1 == 'Large'){
            addition = 100
        }
        if (setting2 == 'Low'){
            factor = 1
        }
        if (setting2 == 'Moderate'){
            factor = 1.5
        }
        if (setting2 == 'High'){
            factor = 2
        }
        this.events[0].demand=(this.events[0].demand+addition)*factor
        this.events[1].demand=(this.events[0].demand+addition)*factor
        this.events[2].demand=(this.events[0].demand+addition)*factor
        }
      }
  }
</script>
